	module.exports = {"data":{"markdownRemark":{"html":"<p>The JavaScript array method <code>.toLowerCase()</code> will return the value of the string converted to lower case. The original string is not changed.</p>\n<p><strong>Syntax</strong></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code>  string<span class=\"token punctuation\">.</span><span class=\"token function\">toLowerCase</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span>\n</code></pre>\n      </div>\n<h2>Examples</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code>  <span class=\"token keyword\">var</span> shout <span class=\"token operator\">=</span> <span class=\"token string\">\"I AM SHOUTING VERY LOUDLY\"</span>\n  <span class=\"token keyword\">var</span> whisper <span class=\"token operator\">=</span> shout<span class=\"token punctuation\">.</span><span class=\"token function\">toLowerCase</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span>\n  \n  console<span class=\"token punctuation\">.</span><span class=\"token function\">log</span><span class=\"token punctuation\">(</span>shout<span class=\"token punctuation\">)</span> <span class=\"token comment\" spellcheck=\"true\">// will return \"I AM SHOUTING VERY LOUDLY\"</span>\n  console<span class=\"token punctuation\">.</span><span class=\"token function\">log</span><span class=\"token punctuation\">(</span>whisper<span class=\"token punctuation\">)</span> <span class=\"token comment\" spellcheck=\"true\">// will return \"i am shouting very loudly\"</span>\n</code></pre>\n      </div>\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<p>Source : <a href='https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/String/toLowerCase' target='_blank' rel='nofollow'>MDN</a></p>","fields":{"slug":"/javascript/standard-objects/string/string-prototype-tolowercase/"},"frontmatter":{"title":"String.prototype.toLowerCase"}}},"pathContext":{"slug":"/javascript/standard-objects/string/string-prototype-tolowercase/"}}

